// ** Custom Components
import Sidebar from '@components/sidebar'

// ** Third Party Components
import { Lock, Edit, Trash2 } from 'react-feather'
import { Button, FormGroup, Label, Input } from 'reactstrap'
import { AvForm, AvInput } from 'availity-reactstrap-validation-safe'
import { useState } from 'react'
import "../../../../assets/scss/views/lanuchpad/lanuchpad.scss"

const newItem = ({ open, toggleSidebar }) => {
  // ** Function to handle form submit
  const onSubmit = (event, errors) => {
    if (!errors.length) {
      toggleSidebar()
    }
    event.preventDefault()
  }
  const [projectLogo, setprojectLogo] = useState('')
  const [blockchainLogo, setblockchainLogo] = useState('')
  const [siteImg, setSiteImg] = useState('')
  const projectLogoSelect = (e) => {
    setprojectLogo(URL.createObjectURL(e.target.files[0]))
  }
  const blockchainLogoSelect = (e) => {
    setblockchainLogo(URL.createObjectURL(e.target.files[0]))
  }
  const siteImgSelect = (e) => {
    setSiteImg(URL.createObjectURL(e.target.files[0]))
  }

  return (
    <Sidebar
      size='lg'
      open={open}
      headerClassName='mb-1'
      contentClassName='pt-0'
      toggleSidebar={toggleSidebar}
    >
      <AvForm onSubmit={onSubmit}>
        <Label for='project-logo'>Project Logo</Label>
        <FormGroup>
          <img id="projectImg" src={projectLogo}></img>

          <Button.Ripple id='change-img' tag={Label} className='mr-75 mb-0' color='primary'>
            <span className='d-none d-sm-block'>Change</span>
            <span className='d-block d-sm-none'>Change
            </span>
            <AvInput name='project-logo' accept='image/*' hidden id='project-logo' onChange={(e) => projectLogoSelect(e)} type="file" required />
          </Button.Ripple>
        </FormGroup>
        <Label for='project-logo'>Blockchain Logo</Label>
        <FormGroup>
          <img id="blockchainImg" src={blockchainLogo}></img>
          <Button.Ripple id='change-img' tag={Label} className='mr-75 mb-0' color='primary'>
            <span className='d-none d-sm-block'>Change</span>
            <span className='d-block d-sm-none'>
              Change
            </span>
            <AvInput name='project-logo' accept='image/*' hidden id='project-logo' onChange={(e) => blockchainLogoSelect(e)} type="file" required />
          </Button.Ripple>
        </FormGroup>

        <FormGroup>
          <Label for='project-name'>Project Name</Label>
          <AvInput name='project-name' id='project-name' placeholder='Staking' required />
        </FormGroup>
        <FormGroup>
          <Label for='token-name'>Token Name</Label>
          <AvInput name='token-name' id='token-name' placeholder='Solview' required />
        </FormGroup>
        <FormGroup>
          <Label for='website'>Website</Label>
          <AvInput name='website' id='website' placeholder='solview.app' required />
        </FormGroup>
        <FormGroup>
          <Label for='twitter'>Twitter</Label>
          <AvInput name='twitter' id='twitter' placeholder='twitter.com' required />
        </FormGroup>
        <FormGroup>
          <Label for='discord'>Discord</Label>
          <AvInput name='discord' id='discord' placeholder='(397) 294-5153' required />
        </FormGroup>
        <FormGroup>
          <Label for='medium'>Medium</Label>
          <AvInput name='medium' id='medium' placeholder='(397) 294-5153' required />
        </FormGroup>
        <FormGroup>
          <Label for='telegram'>Telegram</Label>
          <AvInput name='telegram' id='telegram' placeholder='(397) 294-5153' required />
        </FormGroup>
        <FormGroup>
          <Label for='status'>Status</Label>
          <AvInput type='select' id='status' name='status' required>
            <option value='live'>Live</option>
            <option value='upcoming'>Upcoming</option>
            <option value='ended'>Ended</option>
          </AvInput>
        </FormGroup>
        <Label>Detail</Label>
        <Input type='textarea' name='text' id='exampleText' rows='3' placeholder='Textarea' />
        <Label for='project-logo'>Detail Image</Label>
        <FormGroup>
          <img id="detailImg" src={siteImg}></img>
          <Button.Ripple id='change-img' tag={Label} className='mr-75 mb-0' color='primary'>
            <span className='d-none d-sm-block'>Change</span>
            <span className='d-block d-sm-none'>Change
            </span>
            <AvInput name='project-logo' accept='image/*' hidden id='project-logo' onChange={(e) => siteImgSelect(e)} type="file" required />
          </Button.Ripple>
        </FormGroup>
        <Button type='submit' className='mr-1' color='primary'>
          Save
        </Button>
        <Button type='reset' color='secondary' outline onClick={toggleSidebar}>
          Cancel
        </Button>
      </AvForm>
    </Sidebar>
  )
}

export default newItem
